@import './src/common/styles/sass/variables';
@import './src/common/styles/sass/mixins';

.skill {
    width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
    background-color: #161616;
    position: relative;
    padding: 38px 42px;
    .icon {
        width: 100px;
        height: 100px;
        background-size: cover;
        background-position: center;
    }
    h3 {
        @include text-settings($fontFamily,20px,600,1.5);
        letter-spacing: .1px;
        margin-bottom: 6px;
        color: $textColor;
    }
    .description {
        @include text-settings($fontFamily,14px,400,1.7);
        text-align: left;
        margin: 0px;
        color: #9f9f9f;
    }
    &:hover {
        &:before {
            position: absolute;
            content: '';
            width: 100%;
            height: 7px;
            background-color: #009e66;
            bottom: 0;
        }
    }
}


