@import './src/common/styles/sass/variables';
@import './src/common/styles/sass/mixins';

.contactsBlock {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  min-height: 100vh;

  .form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-width: 50%;
    min-height: 100px;
    color: #777;
    align-items: center;

    .input {
      @include text-settings($fontFamily, 15px, 400, 1.5);
      width: 100%;
      color: #777;
      background-color: #161616;
      border-bottom: 1px solid #1d1d1d;
      border-radius: 0;
      border: none;
      height: 44px;
      padding: 9px 14px;
      margin-bottom: 20px;
      box-sizing: border-box;

      &:focus {
        outline: 1px solid #009e66;
      }
    }

    .textarea {
      @include text-settings($fontFamily, 15px, 400, 1.5);
      width: 100%;
      min-height: 152px;
      background-color: #161616;
      border: none;
      border-bottom: 1px solid #1d1d1d;
      border-radius: 0;
      padding: 9px 14px;
      margin-bottom: 20px;
      color: #777;
      box-sizing: border-box;

      &:focus {
        outline: 1px solid #009e66;
      }
    }

    @media screen and (max-width: 500px) {
      min-width: 100%;
    }
  }

  .button {
    @include text-settings($fontFamily, 14px, 400, 1.5);
    border-radius: 30px;
    background-color: #009e66;
    color: #fff;
    padding: 10px 26px;
    margin-bottom: 20px;
    border: none;
    outline: none;
  }
}


